        class="deivce-batch-add-dialog">

		<el-row :gutter="0">         
            <el-col :span="24">                
                <div class="cmcc-info-text">:excel</div>
                <div class="cmcc-info-text" style="margin-bottom:30px;"><br>
                    ,<br>
                </div>

                <el-upload  style="float:right;"
                    ref="fileUpload"
                    name="uploadFile"
                    accept=".xls,.xlsx"
                    :multiple="false"
                    :action="batchAddUploadURL"
                    :on-success="uploadFileSuccess"
                    :on-error="uploadFileError"
                    :before-upload="uploadFileBeforeUpload">
                    <el-button type="primary" slot="trigger" size="small" :loading="loading"></el-button>
                </el-upload>
                       
            </el-col>
