<script>
	import { mapGetters } from 'vuex';

	import { AdminAPI } from '../../api';

    import * as _ from 'lodash';

    export default {
        data() {
            return {
                modalVisible: true,
                loading: false,               
                batchAddUploadURL: AdminAPI.uploadBlackListUrl,               
            }
        },
        computed: {           
